import { Config } from "../config"
import { ObjectBase } from "@umlaut-games/baem-engine"

export class Beacon extends ObjectBase {
  constructor(
    resources: PIXI.IResourceDictionary,
    height: number,
    width: number
  ) {
    super(resources)

    this._isFacingRight = true

    this.height = height
    this.width = width

    const bg = new PIXI.Sprite(
      Config.highlightBeacons
        ? PIXI.Texture.WHITE
        : PIXI.Texture.EMPTY
    )

    bg.width = width
    bg.height = height

    if (Config.highlightBeacons) {
      bg.tint = 0xff0000
    }

    this.addChild(bg)
  }
}
