import { tween, easing } from "popmotion"
import {
  AnimationBase,
  MathHelper
} from "@umlaut-games/baem-engine"

/**
 *
 */
export class Shell extends AnimationBase {
  constructor(
    resources: PIXI.IResourceDictionary,
    texture: PIXI.Texture
  ) {
    super(resources)

    this.pivot.x = this.width / 2
    this.pivot.y = this.height / 2

    const sprite = new PIXI.Sprite(texture)

    // const t = TweenMax.to(sprite, 2.5, {ease: Back.easeInOut.config(4), y: -200})

    sprite.rotation = MathHelper.getRandomInt(0, 100)

    this.scale.x = 0.1
    this.scale.y = 0.1

    tween({
      from: 0,
      to: -MathHelper.getRandomInt(500, 700),
      duration: 600
    }).start((x: number) => (sprite.x = x))

    tween({
      from: 0,
      to: MathHelper.getRandomInt(900, 1000),
      duration: 600,
      ease: [easing.bounceOut]
    }).start((y: number) => (sprite.y = y))

    this.addChild(sprite)
  }
}
