import { SimpleLightmapFilter } from "@pixi/filter-simple-lightmap"
import { AnimationBase } from "@umlaut-games/baem-engine"

/**
 *
 */
export class Flashlight extends AnimationBase {
  constructor(resources: PIXI.IResourceDictionary) {
    super(resources)

    const texture = PIXI.Texture.from("flashlight.png")
    const lightmap = new SimpleLightmapFilter(texture)
    lightmap.alpha = 0.01
    lightmap.blendMode = PIXI.BLEND_MODES.ADD

    const lightCone = new PIXI.Sprite(texture)
    lightCone.width = 100
    lightCone.height = 50

    lightCone.alpha = 0.2
    lightCone.tint = 0xefebd8

    lightCone.y = -10
    lightCone.x = -20

    lightCone.pivot.x = -(lightCone.height / 2)
    lightCone.pivot.y = 0

    lightCone.zIndex = 99999

    lightCone.filters = [
      new PIXI.filters.NoiseFilter(0.2),
      lightmap
    ]

    this.zIndex = 9999
    this.addChild(lightCone)

    this.scale.set(4, 2)
  }

  tick(delta: number) {
    super.tick(delta)

    this.outline()
  }
}
