import {
  AnimationBase,
  AnimationHelper
} from "@umlaut-games/baem-engine"

/**
 *
 */
export class Blood extends AnimationBase {
  constructor(
    resources: PIXI.IResourceDictionary,
    speed: number = 0.01
  ) {
    super(resources)
    const sheet = "things"

    this._clips = {
      [sheet]: AnimationHelper.loadClip(
        this._resources,
        sheet,
        "blood",
        speed,
        1
      )
    }

    this._playClip(sheet, false, false)
  }
}
