import { ObjectBase } from "@umlaut-games/baem-engine"

export class Crosshair extends ObjectBase {
  constructor(resources: PIXI.IResourceDictionary) {
    super(resources)

    this.zIndex = 3
    this.addChild(
      new PIXI.Text("x", {
        fill: "#fff"
      } as PIXI.TextStyle)
    )
  }
}
