import { AnimationBase } from "@umlaut-games/baem-engine"

/**
 *
 */
export class Impact extends AnimationBase {
  constructor(resources: PIXI.IResourceDictionary) {
    super(resources)

    const resource = this._resources["weapons.effects"]
    const impact = new PIXI.Sprite(
      resource.textures!["impact.png"]
    )

    this.addChild(impact)
  }
}
